export const Contact = () => {
	const [click, setclick] = useState(false);
	useEffect(() => {}, [click]);

	useEffect(() => {
		if (click === true) {
			console.log("value");
			setclick(!click);
			//open link in new tab
			window.open(
				"https://www.linkedin.com/in/antonlnd/",
				"_blank",
				"noopener noreferrer scrollbars=yes "
			);
		}
	}, [click]);

	return (
		<About>
			<h1>Contact</h1>
			// email
			<p>
				<a href="mailto:antonland@pm.me">Email Me</a>
			</p>
			<p>
				<a href="tel:+1-917-379-6365">Call Me</a>
			</p>
			<div className="buttondiv" onClick={(e) => setclick(!click)}>
				LinkedIn
			</div>
			<div className="buttondiv" onClick={(e) => setclick(!click)}>
				GitHub
			</div>
			<div className="buttondiv" onClick={(e) => setclick(!click)}>
				GitLab
			</div>
			<div className="buttondiv" onClick={(e) => setclick(!click)}>
				Projects
			</div>
		</About>
	);
};

